import { getQueryRoute } from 'api/queries/query';
import { addFilterToQuery, removeFilterFromQuery } from './filter';
export const initQuery = (query, reset = false, props = {}) => {
    const newQuery = Object.assign(Object.assign({}, JSON.parse(JSON.stringify(query))), props);
    // Reset pagination
    if (reset) {
        if (newQuery.filter && newQuery.filter.offset !== undefined) {
            newQuery.filter = Object.assign(Object.assign({}, query.filter), { offset: 0 });
        }
        if (newQuery.offset !== undefined) {
            newQuery.offset = 0;
        }
    }
    return newQuery;
};
export const getRouteForQuery = (query, location, reset = false) => {
    const newQuery = initQuery(query, reset);
    return `${location.pathname}?${getQueryRoute(newQuery)}`;
};
export const handleOnCurrencySelected = (query) => {
    return initQuery(query);
};
export const handleOnCostTypeSelected = (query) => {
    return initQuery(query);
};
export const handleOnCostDistributionSelected = (query) => {
    return initQuery(query, false, {
        order_by: undefined, // Clear sort
    });
};
export const handleOnFilterAdded = (query, filter) => {
    const newQuery = initQuery(query, true);
    return addFilterToQuery(newQuery, filter);
};
export const handleOnFilterRemoved = (query, filter) => {
    const newQuery = initQuery(query, true);
    return removeFilterFromQuery(newQuery, filter);
};
export const handleOnPerPageSelect = (query, perPage, isLimit = false) => {
    return initQuery(query, false, Object.assign({}, (isLimit
        ? {
            limit: perPage,
        }
        : {
            filter: Object.assign(Object.assign({}, query.filter), { limit: perPage }),
        })));
};
export const handleOnSetPage = (query, report, pageNumber, isLimit = false) => {
    let limit = 10;
    if (report && report.meta) {
        if (isLimit && report.meta.limit !== undefined) {
            limit = report.meta.limit;
        }
        else if (report.meta.filter && report.meta.filter.limit !== undefined) {
            limit = report.meta.filter.limit;
        }
    }
    const offset = pageNumber * limit - limit;
    return initQuery(query, false, Object.assign({}, (isLimit
        ? {
            limit,
            offset,
        }
        : {
            filter: Object.assign(Object.assign({}, query.filter), { limit,
                offset }),
        })));
};
export const handleOnSort = (query, sortType, isSortAscending, date = undefined) => {
    return initQuery(query, false, {
        order_by: Object.assign({ [sortType]: isSortAscending ? 'asc' : 'desc' }, (date && {
            date,
        })),
    });
};
